 
* {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
} 
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;900&display=swap');

//        loading page         //
.load_background {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  height: 100%;
  background:white;
  .spinner {
    animation: rotate 2s linear infinite;
    z-index: 2;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -25px 0 0 -25px;
    width: 50px;
    height: 50px; 
    & .path {
      stroke: hsl(210, 70, 75);
      stroke-linecap: round;
      animation: dash 1.5s ease-in-out infinite;
    }
    
  }
}.hide{  
  display: none;
  
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}


// ------------- MIXINS ------------- //
@mixin transition($time, $property: all, $easing: ease-in) {
    transition: $property $time $easing;
}

// ------------- VARIABLES ------------- //
$parallax-offset: 30vh;
$content-offset: 40vh;
$transition-speed: 1.2s;
$slide-number: 5;

html, body {
  overflow: hidden;
  font-family: 'Noto Sans KR', sans-serif;
}
.nav_wrap {
    position: fixed;
    width: 100%;
    background: black;
    z-index: 100;
    ul {
        display: flex;
    }
}
.sections {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  overflow: hidden;
  will-change: transform;
  backface-visibility: hidden;
  height: 100vh + $parallax-offset;
  position: fixed;
  width: 100%;
  transform: translateY($parallax-offset);
  @include transition($transition-speed, all, cubic-bezier(0.22, 0.44, 0, 1));
  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
  } 
  &:first-child {
    background-image: url('./asset/greeting.jpg');
    transform: translateY(-$parallax-offset / 2);
    .content-wrapper {
      transform: translateY($parallax-offset /2);
    }
  }
  &:nth-child(2) {
    background-image: url('./asset/about.jpg');
  }
  &:nth-child(3) {
    background-image: url('./asset/port_bg1.jpg');
  }
  &:nth-child(4) {
    background-image: url('./asset/port_bg2.jpg');
  } 
  &:nth-child(5) {
    background-image: url('./asset/contact_bg.jpg'); 
  }
  .greeting{ 
    font-size: 5vh;
    .fade{
        opacity: 1;
    }
  } 
  span {
      opacity: 0;
      transition: all 0.4s ease;
  }
}

.control{
    position: absolute;
    right: 5%;
    top: 70%;
    z-index: 10;
    .prev_btn{ 
        text-indent: 9999px;
        background: white url('./asset/arrow_up.png') no-repeat 50% center;
        background-size: 40%;
        width: 50px;
        height: 50px;
        border-radius: 25px;
        display: block; 
        margin-bottom: 10px;
        cursor: pointer;
    }.next_btn{
        text-indent: 9999px;
        background: white url('./asset/arrow_down.png') no-repeat 50% center;
        background-size: 40%;
        width: 50px;
        height: 50px;
        border-radius: 25px;
        margin-bottom: 10px;
        display: block;
        cursor: pointer;
    }.github{
      background: white url('./asset/logo_github.png') no-repeat 50% center;
      width: 50px;
      text-indent: 9999px;
      height: 50px;
      border-radius: 25px;
      cursor: pointer;
    }
    .un_btn {
      background-color: #999;
    }
}
.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-flow: column nowrap;
  color: #fff;
  font-family: Montserrat;
  text-transform: uppercase;
  transform: translateY($content-offset);
  will-change: transform;
  backface-visibility: hidden;
  @include transition($transition-speed + .5, all, cubic-bezier(0.22, 0.44, 0, 1));
    .down_bar{ 
        overflow: hidden;
        width: 100%;
        padding-top: 30px;
        opacity: 0;
        svg{ 
            position: relative;
            animation: down 2s cubic-bezier(1,0,0,1) infinite; 
            path{
                fill: white;
                stroke: white;
                stroke-linecap: round;
                stroke-opacity: 1;
                stroke-width: 2px; 
            }
        } 
        @keyframes down {
            0%{ 
                top: 0;
                opacity: 1;
            }
            100%{
                opacity: .5;
                top: 100px;
            }
        }
    }
    .about_txt{
      text-align: left;
      padding: 0 60px;
      p{
        font-size: 20px;
        line-height: 28px;
      }
    }
    .about_txt2{
      text-align: right;
      padding: 0 60px;
    }
    .show{
        font-size: 2vh;
        transition: all 1.4s ease;
        opacity: 1;
    } 
    // sec3 start
    .sec3_inner {
      padding: 0 60px;
      display: block;
      height: 100%;
      .protfolio_top{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        div:first-of-type{  
          width: 50%;
          height: 100%;
          padding-right: 30px;
          text-align: left;
          display: flex; 
          align-items: center;
          p{
            font-size: 20px;
            line-height: 28px;
          }
        }
        a{ 
          max-width: 440px;
          min-width: 280px;
          width: 100%;
          height: 80%;
          background: url('./asset/ikea_shot.jpg') no-repeat center/cover;
          box-shadow: 30px 40px 40px rgba(0, 0, 0, 0.6);
        }
      } 
    }
    // sec4 start
    .sec4_inner {
      padding: 0 60px;
      display: block;
      height: 100%;
      .protfolio_top{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        div:first-of-type{   
          width: 50%;
          height: 100%;
          padding-left: 30px;
          text-align: left;
          display: flex;
          justify-content: center;
          align-items: center;
          p{
            font-size: 20px;
            line-height: 28px;
          }
        }
        a{ 
          max-width: 500px;
          min-width: 500px;
          width: 100%;
          height: 80%;
          background: url('./asset/loccitane_shot2.jpg') no-repeat center/cover;
          box-shadow: 30px 40px 40px rgba(0, 0, 0, 0.6);
        }
      } 
    }
} 


/* Set stacking context of slides */

@for $i from 1 to ($slide-number + 1) {
  .sections:nth-child(#{$i}) {
      z-index: ($slide-number + 1) - $i; 
  }
}

// ------------- scrolling ------------- //

.sections.up-scroll {
  transform: translate3d(0,-$parallax-offset / 2,0);
  .wrapper {
    transform: translateY($parallax-offset / 2);
  }
  + .sections {
    transform: translate3d(0,$parallax-offset,0);
      .wrapper {
        transform: translateY($parallax-offset);
      }
    }
}

.sections.down-scroll {
  transform: translate3d(0,-(100vh + $parallax-offset),0);
  .wrapper {
    transform: translateY($content-offset);
  }
  + .sections:not(.down-scroll) {
    transform: translate3d(0,-$parallax-offset / 2,0);
      .wrapper {
        transform: translateY($parallax-offset / 2);
      }
  }
} 
.sec5 {
  .wrapper{
    text-transform: initial;
    .contact {
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      width: 50%;
      height: 200px;
      background: rgba(0,0,0,.3);
      margin: 0 auto;
      &:hover{
       background: rgba(0,0,0,.5); 
      }
      p{
        font-size: 20px;
        line-height: 28px;
      }
      strong{
        font-size: 4vh;
        line-height: 8vh;
      }
    }
  }
}
// 반응형


@media all and (max-width:1023px){
  .sec5 .wrapper .contact {
    width: 80%;
  }
  .wrapper .sec4_inner .protfolio_top div:first-of-type p,.wrapper .about_txt p,.wrapper .sec3_inner .protfolio_top div:first-of-type p{
    font-size: 18px;
    line-height: 26px;
  }
  .wrapper .sec3_inner .protfolio_top a{
    height: 60%;
  }

  .wrapper .sec4_inner .protfolio_top a{
    min-width: 300px;
    height: 50%;
  }
}
@media all and (max-width:767px){
  .wrapper .about_txt{
    padding: 0 20px;
  }
  .sections .greeting{
    font-size: 3vh;
  }
  .wrapper .sec3_inner,.wrapper .sec4_inner {
    padding: 0;
  }
  .wrapper .sec4_inner .protfolio_top div:first-of-type p, .wrapper .about_txt p, .wrapper .sec3_inner .protfolio_top div:first-of-type p{
    z-index: 10;
    font-size: 16px;
    line-height: 24px;
  }
  .wrapper .sec3_inner .protfolio_top {
    flex-direction: column-reverse;
  }
  .wrapper .sec4_inner .protfolio_top{
    flex-direction: column;
  }.wrapper .sec4_inner .protfolio_top a{
    background: url(./asset/logo_loccitane.png) center center / cover no-repeat;
    width: 500px;
    height: 200px;
  }
   .wrapper .sec3_inner .protfolio_top a{
    background: url(./asset/logo_ikea.jpg) center center / cover no-repeat;
    width: 500px;
    height: 200px;
  }
  .wrapper .sec4_inner .protfolio_top div:first-of-type,.wrapper .sec3_inner .protfolio_top div:first-of-type{
    height: 55%;
    padding: 0;
  }
  .wrapper .sec4_inner .protfolio_top div:first-of-type,.wrapper .sec3_inner .protfolio_top div:first-of-type{
    width: 100%; 
  }
  .sec5 .wrapper .contact{
    width: 100%;
  }
}
@media all and (max-width:479px){
  .wrapper .sec3_inner .protfolio_top,.wrapper .sec4_inner .protfolio_top{
    padding: 0 20px;
  }
  .wrapper .sec4_inner .protfolio_top a, .wrapper .sec3_inner .protfolio_top a{
    width: 100%; 
  }
  .sec5 .wrapper .contact strong{
    font-size: 3vh;
    line-height: 6vh;
  }
} 
@media all and (max-width:350px){
 .wrapper .sec3_inner .protfolio_top a{
    height: 30%;
  }
  .wrapper .sec4_inner .protfolio_top a {
    height: 15%;
  }
  .sec5 .wrapper .contact strong{
    font-size: 2.5vh;
    line-height: 5vh;
  }
}